import {
  Box,
  Modal,
  Typography,
  Button,
  TextField,
  FormHelperText,
} from "@mui/material";
import LoadingIndicator from "~/components/common/LoadingIndicator";

import { useState } from "react";
import { useTranslation } from "react-i18next";
import { useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { routes } from "~/routes";
import SurveyService from "~/services/SurveyService";
import styles from "./SurveyClone.module.css";
import { PROCESSED_ERRORS, onApiError } from '~/utils/errorsProcessor';
import { useDispatch } from 'react-redux';

export const SurveyClone = ({ open, onClose }) => {
  const { t } = useTranslation("manage");
  const navigate = useNavigate();
  const survey = useSelector((state) => state.editState.survey);
  const [newSurveyName, setNewSurveyName] = useState("");
  const [newSurveyNameError, setNewSurveyNameError] = useState("");
  const dispatch = useDispatch()

  const onSurveyNameChanged = (e) => {
    setNewSurveyName(e.target.value);
    setNewSurveyNameError("");
  };

  const goDashboard = () => {
    navigate(routes.dashboard);
  };

  const validate = () => {
    setNewSurveyNameError("");

    if (newSurveyName.length == 0) {
      setNewSurveyNameError(t("survey_required"));
      return false;
    }
    if (survey.name === newSurveyName) {
      setNewSurveyNameError(t("processed_errors.duplicate_survey_name"));
      return false;
    }
    return true;
  };

  const cloneSurvey = () => {
    if (!validate()) {
      return;
    }
    setLoading(true);
    const data = {
      name: newSurveyName,
    };
    SurveyService.cloneSurvey(survey.id, data)
      .then(() => {
        onClose();
        goDashboard();
      })
      .catch((e) => {
        onApiError({
          error: e,
          globalErrorHandler: (processedError) => {
            dispatch(onError(processedError));
          },
          locallErrorHandler: (processedError) => {
            if (
              processedError.name == PROCESSED_ERRORS.DUPLICATE_SURVEY_NAME.name
            ) {
              setNewSurveyNameError(t(`processed_errors.${processed.name}`));
            }
          },
        });
      })
      .finally(() => {
        setLoading(false);
      });
  };

  return (
    <>
      <Modal
        sx={{
          ".MuiBackdrop-root": {
            backgroundColor: "rgba(0, 0, 0, 0.3)",
          },
        }}
        open={open}
        onClose={onClose}
      >
        <Box className={styles.wrapper}>
          <Typography>{t("edit_survey.clone_survey")}</Typography>
          <TextField
            error={newSurveyNameError.length > 0}
            sx={{ minWidth: "400px" }}
            required={true}
            value={newSurveyName}
            label={t("label.new_survey_name")}
            onChange={onSurveyNameChanged}
            variant="standard"
          />
          {newSurveyNameError && (
            <FormHelperText className={styles.errorText}>
              {newSurveyNameError}
            </FormHelperText>
          )}
          <Box className={styles.action}>
            <Button onClick={onClose}>{t("action_btn.cancel")}</Button>
            <Button onClick={cloneSurvey}>{t("action_btn.save")}</Button>
          </Box>
        </Box>
      </Modal>
    </>
  );
};
