import { IconButton, Tab, Tabs } from "@mui/material";
import PaletteIcon from "@mui/icons-material/Palette";
import VisibilityIcon from "@mui/icons-material/Visibility";
import TranslateIcon from "@mui/icons-material/Translate";

import { useSelector } from "react-redux";
import SendIcon from "@mui/icons-material/Send";
import styles from "./TopBanner.module.css";
import CloseIcon from "@mui/icons-material/Close";
import { MANAGE_SURVEY_PAGE } from "~/pages/ManageSurvey";
import { rtlLanguage } from "~/utils/common";
import { useTranslation } from "react-i18next";
import ComputerIcon from "@mui/icons-material/Computer";
import PhoneAndroidIcon from "@mui/icons-material/PhoneAndroid";
import WifiOffIcon from "@mui/icons-material/WifiOff";
import { MANAGE_SURVEY_LANDING_PAGES } from "~/routes";
import React, { useState } from "react";
import LaunchDialog from "~/pages/manage/LaunchDialog";
import ManageTranslations from "~/pages/manage/ManageTranslations";
import { Edit, GTranslate, Settings, Visibility } from "@mui/icons-material";

function TopBanner({
  selectedTab,
  previewMode,
  onTabChange,
  page,
  onClose,
  onPreviewModeChanged,
  onPreviewClicked,
  onDesignClicked,
  onStartTranslation,
  onPublish,
  availableTabs,
}) {
  const { i18n } = useTranslation();
  const langInfo = useSelector((state) => {
    return state.designState.langInfo;
  });

  const [tranlsationOpen, setTranlsationOpen] = useState(false);
  const [launchOpen, setLaunchOpen] = useState(false);

  const isSaving = useSelector((state) => {
    return state.designState.isSaving || state.editState.isSaving;
  });

  function component() {
    switch (page) {
      case MANAGE_SURVEY_PAGE.TABS:
        return (
          <>
            <Tabs
              orientation="vertical"
              value={selectedTab}
              onChange={(event, newValue) => {
                onTabChange(newValue);
              }}
              centered
            >
              {availableTabs.indexOf(MANAGE_SURVEY_LANDING_PAGES.DESIGN) !==
                -1 && (
                <Tab
                  sx={{ minWidth: "auto" }}
                  label={
                    <>
                      <Edit sx={{ color: "#fff" }} />
                    </>
                  }
                  value={MANAGE_SURVEY_LANDING_PAGES.DESIGN}
                />
              )}
              {availableTabs.indexOf(MANAGE_SURVEY_LANDING_PAGES.RESPONSES) !==
                -1 && (
                <Tab
                  sx={{ minWidth: "auto" }}
                  label={
                    <>
                      <Visibility sx={{ color: "#fff" }} />
                    </>
                  }
                  value={MANAGE_SURVEY_LANDING_PAGES.RESPONSES}
                />
              )}
              {availableTabs.indexOf(MANAGE_SURVEY_LANDING_PAGES.SETTINGS) !==
                -1 && (
                <Tab
                  sx={{ minWidth: "auto" }}
                  label={
                    <>
                      <Settings sx={{ color: "#fff" }} />
                    </>
                  }
                  value={MANAGE_SURVEY_LANDING_PAGES.SETTINGS}
                />
              )}
            </Tabs>
            {availableTabs.indexOf(MANAGE_SURVEY_LANDING_PAGES.DESIGN) !==
              -1 && (
              <div
                className={
                  rtlLanguage.includes(i18n.language)
                    ? styles["topBanner-rtl"]
                    : styles["topBanner-ltr"]
                }
              >
                <IconButton
                  sx={{ width: "-webkit-fill-available" }}
                  disabled={isSaving}
                  onClick={() => setTranlsationOpen(true)}
                >
                  <GTranslate sx={{ color: "#fff" }} />
                </IconButton>
                <IconButton
                  sx={{ width: "-webkit-fill-available" }}
                  disabled={isSaving}
                  onClick={onPreviewClicked}
                >
                  <VisibilityIcon sx={{ color: "#fff" }} />
                </IconButton>

                <IconButton
                  sx={{ width: "-webkit-fill-available" }}
                  disabled={isSaving}
                  onClick={() => setLaunchOpen(true)}
                >
                  <SendIcon sx={{ color: "#fff" }} />
                </IconButton>
                <IconButton
                  sx={{ width: "-webkit-fill-available" }}
                  disabled={isSaving}
                  onClick={onDesignClicked}
                >
                  <PaletteIcon sx={{ color: "#fff" }} />
                </IconButton>
              </div>
            )}
            {tranlsationOpen && (
              <ManageTranslations
                onManageTranslationsClose={() => {
                  setTranlsationOpen(false);
                }}
                onStartTranslation={onStartTranslation}
              />
            )}
            {launchOpen && (
              <LaunchDialog
                onPublish={onPublish}
                onLaunchDialogClose={() => {
                  setLaunchOpen(false);
                }}
              />
            )}
          </>
        );
      case MANAGE_SURVEY_PAGE.TRANSLATE:
        return (
          <div
            style={{ display: "flex", paddingRight: "8px", paddingLeft: "8px" }}
          >
            <IconButton
              onClick={onClose}
              size="large"
              edge="start"
              color="inherit"
              aria-label="menu"
              sx={{ mr: 2 }}
            >
              <CloseIcon />
            </IconButton>
            <div
              style={{
                textAlign: "center",
                display: "table-cell",
                verticalAlign: "middle",
                width: "100%",
                fontSize: "20px",
                lineHeight: "48px",
              }}
            >
              <span>Translate Survey to {langInfo.lang}</span>
            </div>
          </div>
        );
      case MANAGE_SURVEY_PAGE.PREVIEW:
        return (
          <div
            style={{ display: "flex", paddingRight: "8px", paddingLeft: "8px" }}
          >
            <IconButton
              onClick={onClose}
              size="large"
              edge="start"
              color="inherit"
              aria-label="menu"
              sx={{ mr: 2 }}
            >
              <CloseIcon />
            </IconButton>

            <Tabs
              sx={{ width: "100%" }}
              value={previewMode}
              onChange={(event, newValue) => {
                onPreviewModeChanged(newValue);
              }}
              centered
            >
              <Tab icon={<ComputerIcon />} value="online" />
              <Tab icon={<PhoneAndroidIcon />} value="online-mobile" />
              <Tab icon={<WifiOffIcon />} value="offline" />
            </Tabs>
          </div>
        );
      case MANAGE_SURVEY_PAGE.VIEW_RESPONSE:
        return (
          <div
            style={{ display: "flex", paddingRight: "8px", paddingLeft: "8px" }}
          >
            <IconButton
              onClick={onClose}
              size="large"
              edge="start"
              color="inherit"
              aria-label="menu"
              sx={{ mr: 2 }}
            >
              <CloseIcon />
            </IconButton>
            <div
              style={{
                textAlign: "center",
                display: "table-cell",
                verticalAlign: "middle",
                width: "100%",
                fontSize: "20px",
                lineHeight: "48px",
              }}
            >
              <span>View Response Events</span>
            </div>
          </div>
        );
    }
  }

  return <div className={styles.surveyHeader}>{component()}</div>;
}

export default React.memo(TopBanner);
