import {
  Button,
  Dialog,
  DialogContent,
  DialogTitle,
  IconButton,
} from "@mui/material";

import CloseIcon from "@mui/icons-material/Close";
import EditSurveyGeneral from "~/components/manage/EditSurveyGeneral";
import SurveySharing from "~/components/manage/SurveySharing";
import { useSelector } from "react-redux";
import { useDispatch } from "react-redux";
import DesignService from "~/services/DesignService";
import { newVersionReceived, setSaving } from "~/state/design/designState";

function LaunchDialog({ onLaunchDialogClose, onPublish }) {
  const dispatch = useDispatch();

  const hasFatalErrors = useSelector((state) => {
    return (
      state.designState.Survey?.errors &&
      state.designState.Survey.errors.length > 0
    );
  });

  const versionDto = useSelector((state) => {
    return state.designState.versionDto;
  });

  const published = versionDto?.published;

  const params = new URLSearchParams([
    ["version", versionDto?.version],
    ["sub_version", versionDto?.subVersion],
  ]);

  const publish = () => {
    dispatch(setSaving(true));
    DesignService.publish(params)
      .then((data) => {
        dispatch(setSaving(false));
        dispatch(newVersionReceived(data));
        onPublish();
      })
      .catch((error) => {
        dispatch(setSaving(false));
      });
  };

  return (
    <Dialog fullWidth maxWidth="sm" open={true} onClose={onLaunchDialogClose}>
      <DialogTitle>
        Launch
        <IconButton
          aria-label="close"
          onClick={onLaunchDialogClose}
          sx={{
            position: "absolute",
            right: 8,
            top: 8,
            color: (theme) => theme.palette.grey[500],
          }}
        >
          <CloseIcon />
        </IconButton>
      </DialogTitle>
      <DialogContent>
        <EditSurveyGeneral />
        {!published && (
          <>
            <span>This is still not published</span>
            {!hasFatalErrors && (
              <Button
                onClick={() => {
                  publish();
                }}
              >
                Publish
              </Button>
            )}
          </>
        )}
        <SurveySharing />
      </DialogContent>
    </Dialog>
  );
}

export default LaunchDialog;
