import React, { Suspense } from "react";
import {
  Navigate,
  Route,
  Routes,
  useLocation,
  useParams,
} from "react-router-dom";

import { MANAGE_SURVEY_LANDING_PAGES, routes } from "./routes";
import { lazy } from "react";
import { runStore } from "./store";
import { Provider } from "react-redux";

import TokenService from "./services/TokenService";
import { getparam } from "./networking/run";

import LoadingIndicator from "./components/common/LoadingIndicator";
import { ROLES } from "./constants/roles";
import { ManagePageWrapper } from "./pages/ManagePageWrapper";
import ManageSurvey from "./pages/ManageSurvey";

const Page404 = lazy(() => import("./pages/Page404"));
const ConfirmSignup = lazy(() => import("./pages/manage/ConfirmSignup"));
const ForgotPassword = lazy(() => import("./pages/manage/ForgotPassword"));
const ResetPassword = lazy(() => import("./pages/manage/ResetPassword"));
const Dashboard = lazy(() => import("./pages/manage/Dashboard"));
const Login = lazy(() => import("./pages/manage/Login"));
const ManageUsers = lazy(() => import("./pages/manage/ManageUsers"));
const AddEditUser = lazy(() => import("./pages/manage/AddEditUser"));
const Profile = lazy(() => import("./pages/manage/Profile"));
const CreateSurvey = lazy(() => import("./pages/manage/CreateSurvey"));

const RunSurvey = lazy(() => import("./pages/RunSurvey"));

function Web() {
  return (
    <Routes>
      <Route
        path={routes.runSurvey}
        element={
          <Suspense fallback={<LoadingIndicator />}>
            <Provider store={runStore}>
              <RunSurveyWrapper />
            </Provider>
          </Suspense>
        }
      />
      <Route
        path={routes.previewSurvey}
        element={
          <Suspense fallback={<LoadingIndicator />}>
            <Provider store={runStore}>
              <PreviewSurveyWrapper />
            </Provider>
          </Suspense>
        }
      />
      <Route
        path={routes.previewGuestSurvey}
        element={
          <Suspense fallback={<LoadingIndicator />}>
            <Provider store={runStore}>
              <PreviewGuestSurveyWrapper />
            </Provider>
          </Suspense>
        }
      />
      <Route
        path={routes.designSurvey}
        element={
          <Suspense fallback={<LoadingIndicator />}>
            <ManagePageWrapper>
              <PrivateDesignSurvey
                landingPage={MANAGE_SURVEY_LANDING_PAGES.DESIGN}
              />
            </ManagePageWrapper>
          </Suspense>
        }
      />
      <Route
        path={routes.preview}
        element={
          <Suspense fallback={<LoadingIndicator />}>
            <ManagePageWrapper>
              <PrivateDesignSurvey
                landingPage={MANAGE_SURVEY_LANDING_PAGES.PREVIEW}
              />
            </ManagePageWrapper>
          </Suspense>
        }
      />
      <Route
        path={routes.editSurvey}
        element={
          <Suspense fallback={<LoadingIndicator />}>
            <ManagePageWrapper>
              <PrivateDesignSurvey
                landingPage={MANAGE_SURVEY_LANDING_PAGES.SETTINGS}
              />
            </ManagePageWrapper>
          </Suspense>
        }
      />

      <Route
        path={routes.response}
        element={
          <Suspense fallback={<LoadingIndicator />}>
            <ManagePageWrapper>
              <PrivateDesignSurvey
                landingPage={MANAGE_SURVEY_LANDING_PAGES.RESPONSE}
              />
            </ManagePageWrapper>
          </Suspense>
        }
      />

      <Route
        path={routes.responses}
        element={
          <Suspense fallback={<LoadingIndicator />}>
            <ManagePageWrapper>
              <PrivateDesignSurvey
                landingPage={MANAGE_SURVEY_LANDING_PAGES.RESPONSES}
              />
            </ManagePageWrapper>
          </Suspense>
        }
      />
      <Route
        path={routes.manageUsers}
        element={
          <Suspense fallback={<LoadingIndicator />}>
            <PrivateManageUsers roles={[ROLES.SUPER_ADMIN]}>
              <ManagePageWrapper>
                <ManageUsers />
              </ManagePageWrapper>
            </PrivateManageUsers>
          </Suspense>
        }
      />
      <Route
        path={routes.addUser}
        element={
          <Suspense fallback={<LoadingIndicator />}>
            <PrivateManageUsers roles={[ROLES.SUPER_ADMIN]}>
              <ManagePageWrapper>
                <AddEditUser isEditing={false} />
              </ManagePageWrapper>
            </PrivateManageUsers>
          </Suspense>
        }
      />
      <Route
        path={routes.editUser}
        element={
          <Suspense fallback={<LoadingIndicator />}>
            <PrivateManageUsers roles={[ROLES.SUPER_ADMIN]}>
              <ManagePageWrapper>
                <AddEditUser isEditing={true} />
              </ManagePageWrapper>
            </PrivateManageUsers>
          </Suspense>
        }
      />
      <Route
        path={routes.profile}
        element={
          <Suspense fallback={<LoadingIndicator />}>
            <PrivateComponent>
              <ManagePageWrapper>
                <Profile />
              </ManagePageWrapper>
            </PrivateComponent>
          </Suspense>
        }
      />
      <Route
        path={routes.createSurvey}
        element={
          <Suspense fallback={<LoadingIndicator />}>
            <PrivateManageUsers roles={[ROLES.SUPER_ADMIN, ROLES.SUPER_ADMIN]}>
              <ManagePageWrapper>
                <CreateSurvey />
              </ManagePageWrapper>
            </PrivateManageUsers>
          </Suspense>
        }
      />
      <Route
        path={routes.page404}
        element={
          <Suspense fallback={<LoadingIndicator />}>
            <Page404 />
          </Suspense>
        }
      />
      <Route
        path={routes.dashboard}
        element={
          <Suspense fallback={<LoadingIndicator />}>
            <PrivateComponent>
              <ManagePageWrapper>
                <Dashboard />
              </ManagePageWrapper>
            </PrivateComponent>
          </Suspense>
        }
      />

      <Route
        path={routes.login}
        element={
          <Suspense fallback={<LoadingIndicator />}>
            <ManagePageWrapper>
              <Login />
            </ManagePageWrapper>
          </Suspense>
        }
      />
      <Route
        path={routes.signup}
        element={
          <Suspense fallback={<LoadingIndicator />}>
            <ManagePageWrapper>
              <Login />
            </ManagePageWrapper>
          </Suspense>
        }
      />
      <Route
        path={routes.confirmSignup}
        element={
          <Suspense fallback={<LoadingIndicator />}>
            <ManagePageWrapper>
              <ConfirmSignup />
            </ManagePageWrapper>
          </Suspense>
        }
      />
      <Route
        path={routes.forgotPassword}
        element={
          <Suspense fallback={<LoadingIndicator />}>
            <ManagePageWrapper>
              <ForgotPassword />
            </ManagePageWrapper>
          </Suspense>
        }
      />
      <Route
        path={routes.resetPassword}
        element={
          <Suspense fallback={<LoadingIndicator />}>
            <ManagePageWrapper>
              <ResetPassword />
            </ManagePageWrapper>
          </Suspense>
        }
      />
    </Routes>
  );
}

const PrivateDesignSurvey = ({ landingPage }) => {
  const params = useParams();
  sessionStorage.setItem("surveyId", params.surveyId);
  const location = useLocation();
  return TokenService.isAuthenticated() ? (
    <ManageSurvey landingPage={landingPage} />
  ) : (
    <Navigate to="/login" replace state={{ from: location }} />
  );
};

const PrivateComponent = ({ children }) => {
  const location = useLocation();
  return TokenService.isAuthenticated() ? (
    children
  ) : (
    <Navigate to="/login" replace state={{ from: location }} />
  );
};

const PrivateManageUsers = ({ roles, children }) => {
  const location = useLocation();
  const user = TokenService.getUser();
  let hasCorrectRole = false;
  user.roles.forEach((el) => {
    if (roles.indexOf(el) > -1) {
      hasCorrectRole = true;
    }
  });

  if (!hasCorrectRole) {
    return <Navigate to="/" replace />;
  }

  return TokenService.isAuthenticated() ? (
    children
  ) : (
    <Navigate to="/login" replace state={{ from: location }} />
  );
};

const RunSurveyWrapper = () => {
  const surveyId = getparam(useParams(), "surveyId");
  sessionStorage.setItem("surveyId", surveyId);
  return <RunSurvey />;
};

const PreviewSurveyWrapper = () => {
  const surveyId = getparam(useParams(), "surveyId");
  sessionStorage.setItem("surveyId", surveyId);
  const location = useLocation();
  const searchParams = new URLSearchParams(location.search);
  const mode = searchParams.get("mode") || "online";
  return <RunSurvey preview={true} mode={mode} />;
};

const PreviewGuestSurveyWrapper = () => {
  const surveyId = getparam(useParams(), "surveyId");
  sessionStorage.setItem("surveyId", surveyId);
  const location = useLocation();
  const searchParams = new URLSearchParams(location.search);
  const mode = searchParams.get("mode") || "online";
  return <RunSurvey preview={true} guest={true} mode={mode} />;
};

export default Web;
