import React, { Suspense, useEffect, useState } from "react";

import TopBanner from "~/components/design/TopBanner";
import { useDispatch } from "react-redux";
import {
  designStateReceived,
  onAddComponentsVisibilityChange,
  onResetLang,
  resetSetup,
  setup,
} from "~/state/design/designState";
import { GetData } from "~/networking/design";
import {
  onError,
  permissionsLoading,
  permissionsReceived,
  setLoading,
  surveyReceived,
} from "~/state/edit/editState";
import SurveyService from "~/services/SurveyService";
import { onApiError } from "~/utils/errorsProcessor";
import SavingSurvey from "~/components/design/SavingSurvey";

import { surveySetup } from "~/constants/design";

import {
  isAnalyst,
  isAnalystOnly,
  isSurveyAdmin,
  isSurveyorOnly,
} from "~/constants/roles";
import TokenService from "~/services/TokenService";
import { useNavigate, useParams, useSearchParams } from "react-router-dom";
import { MANAGE_SURVEY_LANDING_PAGES } from "~/routes";
import { Box, CircularProgress } from "@mui/material";

const ResponsesSurvey = React.lazy(() => import("../manage/ResponsesSurvey"));
const EditSurvey = React.lazy(() => import("../manage/EditSurvey"));
const DesignSurvey = React.lazy(() => import("../DesignSurvey"));
const ResponsesEvent = React.lazy(() => import("../manage/ResponseEvent"));
const PreviewSurvey = React.lazy(() => import("../PreviewSurvey"));

function ManageSurvey({ landingPage }) {
  const params = useParams();
  const [searchParams, setSearchParams] = useSearchParams();
  const user = TokenService.getUser();
  const [selectedTab, setSelectedTab] = useState(landingTab(landingPage, user));
  const [page, setPage] = useState(rolesToLandingPage(landingPage, user));
  const [previewMode, setPreviewMode] = useState(
    searchParams.get("mode") || "online"
  );
  const [viewEventResponseId, setViewEventResponseId] = useState(null);
  const [designAvailable, setDesignAvailable] = useState(false);
  const [settingAvailable, setSettingAvailable] = useState(false);

  const dispatch = useDispatch();
  const navigate = useNavigate();

  const setState = (state) => {
    dispatch(designStateReceived(state));
  };
  console.log(page);

  const processApirror = (e) => {
    dispatch(setLoading(false));
    onApiError({
      error: e,
      globalErrorHandler: (processedError) => {
        dispatch(onError(processedError));
      },
    });
  };

  useEffect(() => {
    dispatch(onAddComponentsVisibilityChange(true));
    dispatch(resetSetup());
    if (!isSurveyAdmin(user)) {
      return;
    }
    dispatch(setLoading(true));
    GetData(setState, processApirror)
      .then((data) => {
        if (data) {
          setDesignAvailable(true);
        }
      })
      .catch((err) => {
        dispatch(setLoading(false));
      });
    loadSurvey();
  }, []);

  const loadSurvey = () => {
    SurveyService.getSurvey()
      .then((data) => {
        if (data) {
          dispatch(surveyReceived(data));
          loadPermissions(data.id);
        }
      })
      .catch((err) => {
        processApirror(err);
      });

    dispatch(permissionsLoading(true));
  };

  const loadPermissions = (surveyId) => {
    SurveyService.getSurveyPermissionAll(surveyId)
      .then((data) => {
        if (data) {
          setSettingAvailable(true);
          dispatch(permissionsReceived(data));
        }
        dispatch(permissionsLoading(false));
        dispatch(setLoading(false));
      })
      .catch((err) => {
        processApirror(err);
      });
  };

  const shouldShowDesign =
    ((selectedTab == MANAGE_SURVEY_LANDING_PAGES.DESIGN &&
      page === MANAGE_SURVEY_PAGE.TABS) ||
      page === MANAGE_SURVEY_PAGE.TRANSLATE) &&
    designAvailable;

  const shouldShowResponseEvents =
    selectedTab == MANAGE_SURVEY_LANDING_PAGES.RESPONSES &&
    page === MANAGE_SURVEY_PAGE.VIEW_RESPONSE &&
    (viewEventResponseId || params.responseId);

  const shouldShowResponses =
    selectedTab == MANAGE_SURVEY_LANDING_PAGES.RESPONSES &&
    page === MANAGE_SURVEY_PAGE.TABS;

  const shouldShowEditSurvey =
    selectedTab == MANAGE_SURVEY_LANDING_PAGES.SETTINGS &&
    settingAvailable &&
    page === MANAGE_SURVEY_PAGE.TABS;

  const onResponseSelected = (responseId) => {
    window.history.replaceState(
      null,
      "",
      `/${MANAGE_SURVEY_LANDING_PAGES.RESPONSE}/${params.surveyId}/${responseId}`
    );
    setPage(MANAGE_SURVEY_PAGE.VIEW_RESPONSE);
    setViewEventResponseId(responseId);
  };

  const onBannerClosed = () => {
    if (isSurveyorOnly(user)) {
      navigate("/");
      return;
    }
    if (selectedTab == MANAGE_SURVEY_LANDING_PAGES.PREVIEW) {
      const newTab = landingTab(MANAGE_SURVEY_LANDING_PAGES.DESIGN, user);
      setSelectedTab(newTab);
      window.history.replaceState(null, "", `/${newTab}/${params.surveyId}`);
    } else {
      window.history.replaceState(
        null,
        "",
        `/${selectedTab}/${params.surveyId}`
      );
    }

    setViewEventResponseId(null);
    dispatch(onResetLang());
    setPage(MANAGE_SURVEY_PAGE.TABS);
  };

  return (
    <Box sx={{ display: "flex", gap: "1rem" }}>
      <TopBanner
        onDesignClicked={() => {
          setSelectedTab(MANAGE_SURVEY_LANDING_PAGES.DESIGN);
          dispatch(onAddComponentsVisibilityChange(false));
          dispatch(setup(surveySetup));
        }}
        page={page}
        onPreviewClicked={() => {
          window.history.replaceState(
            null,
            "",
            `/preview/${params.surveyId}?mode=online`
          );
          setPage(MANAGE_SURVEY_PAGE.PREVIEW);
        }}
        onPublish={() => loadSurvey()}
        onStartTranslation={() => setPage(MANAGE_SURVEY_PAGE.TRANSLATE)}
        onPreviewModeChanged={(mode) => {
          window.history.replaceState(
            null,
            "",
            `/preview/${params.surveyId}?mode=${mode}`
          );
          setPreviewMode(mode);
        }}
        previewMode={previewMode}
        availableTabs={availableTabs(user)}
        selectedTab={selectedTab}
        onClose={onBannerClosed}
        onTabChange={(tab) => {
          setSelectedTab(tab);
          window.history.replaceState(null, "", `/${tab}/${params.surveyId}`);
        }}
      />
      <Suspense fallback={<CircularProgress />}>
        <Box sx={{ width: "100%", backgroundColor: "#f7f8fc" }}>
          {shouldShowResponses ? (
            <ResponsesSurvey viewEvents={onResponseSelected} />
          ) : shouldShowEditSurvey ? (
            <EditSurvey />
          ) : page === MANAGE_SURVEY_PAGE.PREVIEW ? (
            <PreviewSurvey mode={previewMode} />
          ) : shouldShowResponseEvents ? (
            <ResponsesEvent
              responseId={viewEventResponseId || params.responseId}
            />
          ) : shouldShowDesign ? (
            <DesignSurvey />
          ) : (
            <></>
          )}
        </Box>
      </Suspense>
      <SavingSurvey />
    </Box>
  );
}
export default React.memo(ManageSurvey);

export const MANAGE_SURVEY_PAGE = {
  TABS: "tabs",
  PREVIEW: "preview",
  TRANSLATE: "translate",
  VIEW_RESPONSE: "view_responses",
};

const rolesToLandingPage = (landingPage, user) => {
  if (landingPage == MANAGE_SURVEY_LANDING_PAGES.PREVIEW) {
    return MANAGE_SURVEY_PAGE.PREVIEW;
  } else if (
    (isSurveyAdmin(user) || isAnalyst(user)) &&
    landingPage == MANAGE_SURVEY_LANDING_PAGES.RESPONSE
  ) {
    return MANAGE_SURVEY_PAGE.VIEW_RESPONSE;
  } else if (isSurveyAdmin(user) || isAnalyst(user)) {
    return MANAGE_SURVEY_PAGE.TABS;
  }
  return MANAGE_SURVEY_PAGE.PREVIEW;
};

const availableTabs = (user) => {
  if (isSurveyAdmin(user)) {
    return [
      MANAGE_SURVEY_LANDING_PAGES.DESIGN,
      MANAGE_SURVEY_LANDING_PAGES.RESPONSES,
      MANAGE_SURVEY_LANDING_PAGES.SETTINGS,
    ];
  } else if (isAnalyst(user)) {
    return [MANAGE_SURVEY_LANDING_PAGES.RESPONSES];
  } else {
    return [];
  }
};

export const landingTab = (landingPage, user) => {
  if (landingPage == MANAGE_SURVEY_LANDING_PAGES.PREVIEW) {
    return MANAGE_SURVEY_LANDING_PAGES.PREVIEW;
  } else if (isAnalystOnly(user)) {
    return MANAGE_SURVEY_LANDING_PAGES.RESPONSES;
  } else if (
    isSurveyAdmin(user) &&
    landingPage == MANAGE_SURVEY_LANDING_PAGES.RESPONSE
  ) {
    return MANAGE_SURVEY_LANDING_PAGES.RESPONSES;
  } else if (isSurveyAdmin(user)) {
    return landingPage;
  } else {
    return MANAGE_SURVEY_LANDING_PAGES.NONE;
  }
};
