import React, { useEffect, useState } from "react";
import { Alert, Snackbar } from "@mui/material";
import { useTranslation } from "react-i18next";
import { rtlLanguage } from "~/utils/common";

export default function SuccessSnackbar({ handleClose, success }) {
  const [dir, setDir] = useState("ltr");
  const { t, i18n } = useTranslation("manage");

  useEffect(() => {
    if (rtlLanguage.includes(i18n.language)) {
      setDir("rtl");
    } else {
      setDir("ltr");
    }
  }, [i18n.language]);

  return (
    <Snackbar open={true} autoHideDuration={2000} onClose={handleClose}>
      <Alert
        variant="standard"
        severity={"success"}
        onClose={handleClose}
        dir={dir}
      >
        {t(`processed_successes.${success}`)}
      </Alert>
    </Snackbar>
  );
}
